<template>
    <div :class="{'outBox':true,'align-left':!myself,'align-right':myself}">
        <div :class="{'speaker':!myself,'myself':myself}">{{ this.speaker }}</div>
        <div class="msgBox">{{ this.msg }}{{ this.filename }}<span v-if="this.type=='file' &&this.myself ==false" style="color:green;cursor: pointer;" @click="download"> {{ this.downloading?"停止":"下载"}}</span></div>
        
        <ProgressBar v-if="this.type=='file' &&this.myself ==false" :progress="progress"></ProgressBar>
    </div>
</template>
<script>


import ProgressBar from './progressBar.vue';
export default {
    props: {
    type: { type:String},
    speaker: { type: String },
    myself: { Type: Boolean },
    msg: { type: String },
    progress:{type:Number},
    filename:{type:String},
    },
    data(){
        return{
            downloading:false
        }
    },
    methods:{
        download(){
            this.downloading = !this.downloading
            this.$emit("downloadHandler",this.filename)
        }
    },
    components: { ProgressBar }

}
</script>
<style scoped>
    .outBox{
        padding:2px 20px;
        text-align: center;
        font-size: medium;
        font-weight: bold;
        letter-spacing:2px;
        line-height:40px;
        display: flex;
        flex-direction: column;
    }
    .align-left{
        align-items: start;
    }
    .align-right{
        align-items:end;
    }
    .speaker{
        width:20%;
        color:rgb(26, 228, 255);

    }
    .myself{
        width:20%;
        color:rgb(26, 228, 255);

    }
    .msgBox{
        color:white;
        padding-right:20px;
        white-space:pre-line;
 }
</style>