<template>
    <div class="progress-bar">
      <div class="progress" :style="{ width: progress + '%' }"></div>
    </div>
  </template>
  <script>
  export default {
    props:{
      progress:{type:Number}
    }

  }
  </script>
  <style>
  .progress-bar {
    width: 200px;
    height: 20px;
    background-color: #f5f5f5;
    border-color: black ;
    border:solid;
    border-width: 5px 10px 3px 15px;
    overflow: hidden;
  }
  .progress {
    height: 100%;
    background-color: #1dfbf0;
    transition: width 0.3s ease-in-out;
  }
  </style>