      <div class="friendList">
        <Friend_Item v-for="item in friendList" @switchDialog="switchDialog" :chosen="displayDialog==item.name" :key="item.id" :id="item.id" :name="item.name" :group="false" />
          <Friend_Item v-for="item in groupList" @switchDialog="switchDialog" :chosen="displayDialog==item.name" :key="item.id" :id="item.id" :name="item.name"
          :group="true" />
      </div>
      <div class="chatBox" :style="{ translate:showChatBox?'15%':'120%'}">
        <button class="goback" @click="showChatBox = !showChatBox;" style="background-color:azure; border-left:5px yellow solid;height:5%;width: 10%;position: absolute;left:-2%;transition-duration: 0.4s;font-weight: bold;" :style="{top:showChatBox?'5%':'90%'}"><p style="transform: skew(20deg);">{{ this.showChatBox?"返回":"展开"}}</p></button>
        <button @click="sendMsg" style="background-color:azure; border-right:5px aqua solid;height:5%;width: 10%;position: absolute;right:-2%;transition-duration: 0.4s; bottom: 2%;font-weight: bold" ><p style="transform: skew(20deg);">发送</p></button>
        <button @click="showfilebox = !showfilebox" style="background-color:azure; border-right:5px aqua solid;height:5%;width: 10%;position: absolute;right:-2%;transition-duration: 0.4s; bottom: 10%;font-weight: bold" ><p style="transform: skew(20deg);">文件</p></button>
