          name:"粉红5人",
          id:"平克顿侦探事务所",
          msgList:[]
        }
      ],
      currentMsgList: [],
  
    };
  },
  destroyed(){
    window.clearInterval(this.timer)
  },  
  // components: { Friend_Item, ToolButton, MsgBubble,Add_chat_button,}
  components:{Friend_Item,MsgBubble,fileSendingCard }
}
</script>
